import {i18n} from './common/i18n-setup.js'
import {loadLanguageAsync} from './common/i18n-setup.js'

router.beforeEach((to, from, next) => {
  //const lang = to.params.lang
  const lang = 'de'
  loadLanguageAsync(lang).then(() => next())  
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')



